.conditions {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 80px;
}

.conditions_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 110%;
  text-align: center;
  color: #3E3E3E;
  width: 100%;
  margin-bottom: 20px;
}

.conditions_subtitle {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #3E3E3E;
  margin-bottom: 40px;
  width: 100%;
}

.conditions_item {
  flex-basis: 23.07%;
  display: flex;
  flex-direction: column;
}

.conditions_img {
  margin-bottom: 24px;
}

.conditions_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 110%;
  color: #3E3E3E;
  margin-bottom: 16px;
}

.conditions_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  color: #3E3E3E;
}

@media(max-width: 990px) {
  .conditions_subtitle {
    margin-bottom: 0;
  }
  .conditions_item {
    flex-basis: 47.9%;
    margin-top: 40px;
  }
}

@media(max-width: 767px) {
  .conditions {
    padding-top: 60px;
    flex-direction: column;
    align-items: center;
  }

  .conditions_title {
    font-size: 26px;
    line-height: 110%;
    margin-bottom: 16px;
  }

  .conditions_subtitle {
    font-size: 16px;
    line-height: 140%;

    & br {
      display: none;
    }
  }

  .conditions_item {
    align-items: center;
    margin-top: 30px;
  }

  .conditions_img {
    width: 50px;
    margin-bottom: 14px;
  }

  .conditions_description {
    font-size: 18px;
    line-height: 110%;
    margin-bottom: 10px;
    text-align: center;
  }

  .conditions_text {
    text-align: center;
  }
}