body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: black;
  min-width: 320px;
  min-height: 100vh;
}

.center {
  max-width: 1200px;
  width: calc(100% - 30px);
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  box-sizing: border-box;
}

@media(max-width: 990px) {
  .center {
    max-width: 720px;
  }
}

@media(max-width: 767px) {
  .center {
    max-width: none;
    margin-right: 0;
    margin-left: 0;
    width: 100%;
  }
}

*:hover,
*:focus,
*:active {
  outline: 0;
  outline-offset: 0;
}

p {
  margin: 0;
}

.valid-error {
  color: red;
  font-size: 11px;
}


.loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0,0,0,.3);
  display: none;

  &::before {
    content: url(../img/loader.svg);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &.active {
    z-index: 9999999999;
    display: block;
    pointer-events: all;
  }
}

.disable_page {
  background: white;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: calc(100% - 30px);
  height: 100%;
  z-index: 99999999;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 15px;
  padding-left: 15px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 28px;
  font-weight: bold;
  line-height: 120%;

  & span {
    font-weight: normal;
    margin-top: 5px;
  }

  &.active {
    display: flex;
  }
}

@media(max-width: 767px) {
  .disable_page {
    font-size: 18px;
  }
}
