.tarifs {
  padding-top: 80px;
}

.tarifs_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 110%;
  text-align: center;
  color: #3E3E3E;
  margin-bottom: 40px;
}

.tarifs_table {
  overflow-x: scroll;
  display: block;
  -ms-overflow-style: none;
  scrollbar-width: none;
  border-top: 1px solid #CFD4D7;
  border-left: 1px solid #CFD4D7;
  border-right: 1px solid #CFD4D7;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  &::-webkit-scrollbar {
    width: 0;
    display: none;
  }
}

.tarifs_row {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  color: #3E3E3E;

  &--title {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 110%;
    color: #3E3E3E;
  }
}

.tarifs_cell {
  flex-shrink: 0;
  background: #E8EDED;
  padding: 18px 32px;
  box-sizing: border-box;
  border-bottom: 1px solid #CFD4D7;

  &--common {
    border-bottom: none;
  }

  &:nth-of-type(1) {
    flex-basis: 306px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 110%;
    color: #3E3E3E;
  }

  &:nth-of-type(2) {
    flex-basis: 266px;
  }

  &:nth-of-type(3) {
    flex-basis: 598px;
  }

  &:not(:last-of-type) {
    border-right: 1px solid #CFD4D7;
  }
}

@media(max-width: 990px) {
  .tarifs_cell {
    padding: 18px;
  
    &:nth-of-type(1) {
      flex-basis: 206px;
    }
  
    &:nth-of-type(2) {
      flex-basis: 216px;
    }
  
    &:nth-of-type(3) {
      flex-basis: 268px;
    }
  }
}

@media(max-width: 767px) {
  .tarifs {
    padding-top: 60px;
  }

  .tarifs_title {
    font-size: 26px;
    line-height: 110%;
    margin-bottom: 30px;
  }

  .tarifs_cell {
    padding: 14px;
  
    &:nth-of-type(1) {
      flex-basis: 33.3%;
      min-width: 178px;
    }
  
    &:nth-of-type(2) {
      flex-basis: 27.7%;
      min-width: 148px;
    }
  
    &:nth-of-type(3) {
      min-width: 208px;
      flex-basis: 38.9%;
    }
  }
}

.tarifs_cell {
  padding: 14px;

  &:nth-of-type(1) {
    flex-basis: 33.3%;
  }

  &:nth-of-type(2) {
    flex-basis: 27.7%;
  }

  &:nth-of-type(3) {
    flex-basis: 38.9%;
    flex-grow: 1;
  }
}