.apps {
  margin-top: 80px;
  background: #F5F5F5;
  padding: 80px 0;
  display: flex;
  flex-direction: column;

  & .center {
    display: flex;
    flex-direction: column;
  }
}

.apps_column {
  flex-basis: 460px;
}

.apps_row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 80px;
}

.apps_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 110%;
  color: #3E3E3E;
  margin-bottom: 28px;
}

.apps_list {
  margin: 0;
  padding-left: 20px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  color: #3E3E3E;
}

.apps_point:not(:last-of-type) {
  margin-bottom: 5px;
}

.apps_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  color: #3E3E3E;
}

.apps_button {
  background: #F03226;
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  padding: 18px 0;
  min-width: 240px;
  align-self: center;
  border: 1px solid #F03226;
  box-sizing: border-box;

  &:hover {
    cursor: pointer;
    background: white;
    color: #F03226;
  }
}

.apps_img {
  margin-right: 10px;
}

.apps_row--revers .apps_img {
  margin-right: 0;
  margin-left: 10px;
}

@media(max-width: 990px) {
  .apps_row {
    flex-direction: column;
    align-items: center;
  }

  .apps_column {
    align-self: flex-start;
    flex-basis: auto;
  }

  .apps_img {
    margin-bottom: 18px;
  }

  .apps_row--revers {
    & .apps_column {
      order: 2;
    }

    & .apps_img {
      order: 1
    }
  }

  .apps_img {
    margin-right: 0;
    width: 100%;
  }
  
  .apps_row--revers .apps_img {
    margin-left: 0;
  }
}

@media(max-width: 767px) {
  .apps {
    margin-top: 60px;
    padding: 60px 0;
  }

  .apps_title {
    font-size: 22px;
    line-height: 110%;
    margin-bottom: 14px;
  }

  .apps_row {
    margin-bottom: 40px;
  }
}