.steps {
  padding: 80px 125px;
  background: #F5F5F5;
  margin-top: 80px;
  display: flex;
  flex-direction: column;

  & .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.steps_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 110%;
  text-align: center;
  color: #3E3E3E;
  margin-bottom: 40px;
}

.steps_wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.steps_item {
  flex-basis: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.steps_arrow {
  margin-top: 10px;
}

.steps_img {
  margin-bottom: 24px;
}

.steps_subtitle {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 110%;
  text-align: center;
  color: #3E3E3E;
  margin-bottom: 16px;
}

.steps_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #3E3E3E;
}

.steps_button {
  background: #F03226;
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  padding: 18px 0;
  min-width: 240px;
  border: 1px solid #F03226;
  box-sizing: border-box;

  &:hover {
    cursor: pointer;
    background: white;
    color: #F03226;
  }
}

@media(max-width: 767px) {
  .steps {
    padding: 60px 0;
    margin-top: 60px;
  }

  .steps_title {
    font-size: 26px;
    line-height: 110%;
  }

  .steps_wrapper {
    flex-direction: column;
    align-items: center;
  }

  .steps_item {
    margin-bottom: 30px;
  }

  .steps_img {
    width: 40px;
    margin-bottom: 14px;
  }

  .steps_subtitle {
    font-size: 18px;
    line-height: 110%;
    margin-bottom: 10px;
  }

  .steps_arrow {
    transform: rotate(90deg);
    margin-top: 0;
    margin-bottom: 30px;
  }
}