.footer {
  background: #595959;
  padding-top: 60px;
  padding-bottom: 60px;
}

.footer_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: #999999;
  margin-bottom: 10px;

  & a {
    color: #999999;

    &:hover {
      color:  #F03226;
    }
  }
}

.footer_link {
  margin-top: 20px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: #FFFFFF;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;

  &:hover {
    cursor: pointer;
    color: #F03226;

    & .footer_logo {
      fill: #F03226;
    }
  }
}

.footer_logo {
  margin-left: 4px;
}

@media(max-width: 767px) {
  .footer_logo {
    width: 84px;
  }
}