.advantages {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 80px;
}

.advantages_item {
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #3E3E3E;
  flex-basis: 21.36%;

  & span {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 120%;
    text-align: center;
    color: #F03226;
    margin-bottom: 10px;
  }
}

@media(max-width: 990px) {
  .advantages {
    padding-left: 80px;
    padding-right: 80px;
    flex-wrap: wrap;
    padding-top: calc(80px - 40px);
  }

  .advantages_item {
    flex-basis: 44.6%;
    margin-top: 40px;
  }
}

@media(max-width: 767px) {
  .advantages {
    padding-left: 0;
    padding-right: 0;
    flex-direction: column;
    padding-top: calc(40px - 25px);
  }

  .advantages_item {
    margin-top: 25px;

    & span {
      margin-bottom: 4px;
    }
  }
}