.feedback {
  background: #F5F5F5;
  padding: 80px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.feedback_form {
  background: white;
  box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding: 45px 40px;
  display: flex;
  flex-direction: column;
  max-width: 600px;
  width: 100%;
  box-sizing: border-box;
}

.modal .feedback_form {
  padding-top: 45px;
}

.feedback_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 120%;
  text-align: center;
  color: #3E3E3E;  
  margin-bottom: 14px;
}

.feedback_subtitle {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #3E3E3E;
  margin-bottom: 12px;
}

.feedback_wrapper {
  padding: 0 55px;
  display: flex;
  flex-direction: column;
}


.feedback_text-input {
  border: 1px solid #B2B2B2;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 15px 18px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 110%;
  color: black;
  appearance: none;
  margin-top: 12px;

  &:hover,
  &:focus {
    border-color: #242424;
  }

  &::placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 110%;
    color: #999999;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat,  white;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat,  white;
  }

  &[type="number"] {
    -moz-appearance: textfield;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }
}

#accepted,
#modal_accepted {
  display: none;
}

.feedback_accepted {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 110%;
  color: #000000;
  position: relative;
  padding-left: 36px;
  margin-top: 15.5px;

  &::before {
    content: "";
    display: inline-block;
    border: 1px solid #B2B2B2;
    box-sizing: border-box;
    border-radius: 2px;
    width: 24px;
    height: 24px;
    position: absolute;
    top: calc(50% - 12px);
    left: 0;
  }

  &:hover {
    cursor: pointer;
  }

  & a {
    color: #1C75A8;
  }
}

#accepted:checked + .feedback_accepted::before,
#modal_accepted:checked + .feedback_accepted::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 6.14286L6.15789 10L15 1' stroke='%23F03226' stroke-width='2'/%3E%3C/svg%3E%0A") center no-repeat;
}

.feedback_button {
  background: #F03226;
  border-radius: 4px;
  margin-top: 33.5px;
  padding: 18px 0;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 110%;
  color: #FFFFFF;
  border: 1px solid #F03226;
  align-self: center;
  min-width: 240px;
  box-sizing: border-box;

  &:hover {
    cursor: pointer;
    background: white;
    color: #F03226;
  }

  &:disabled {
    opacity: 0.3;

    &:hover {
      cursor: default;
      background: #F03226;
      color: #FFFFFF;

    }
  }
}

.feedback_prompt {
  font-size: 14px;
  margin-top: 5px;
  line-height: 140%;
}

@media(max-width: 767px) {
  .feedback {
    padding: 60px 15px;
  }

  .feedback_form {
    max-width: none;
    padding: 30px 15px;
  }

  .feedback_title {
    font-size: 22px;
    line-height: 110%;
    margin-bottom: 14px;
  }

  .feedback_subtitle {
    font-size: 16px;
    line-height: 120%;
    margin-bottom: 4px;
  }

  .feedback_button {
    margin-top: 20px;
    width: 100%;
  }

  .feedback_wrapper {
    padding: 0 ;
    display: flex;
    flex-direction: column;
  }

  .feedback_prompt {
    font-size: 12px;
    line-height: 140%;
  }

  .feedback_accepted {
    font-size: 13px;
  }
}