.questions {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 125px;
}

.questions_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 110%;
  text-align: center;
  color: #3E3E3E;
  margin-bottom: 40px;
}

.questions_item {
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  padding-top: 28px;
  padding-bottom: 14px;
  width: 100%;
}

.quesrions_checkbox {
  display: none;
}

.questions_question {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  color: #3E3E3E;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 14px;

  &:hover {
    cursor: pointer;
    color: #F03226;
  }

  & svg {
    transform: rotate(45deg);
    transition: transform 0.3s ease-in-out;
    flex-shrink: 0;
    margin-left: 5px;
  }
}

.question_answer {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 150%;
  color: #3E3E3E;
  padding-right: 100px;
  font-weight: 300;
  overflow: hidden;
  max-height: 0;
}

.quesrions_checkbox:checked + .questions_question svg {
  transform: rotate(0deg);
  transition: transform 0.3s ease-in-out;
}

.quesrions_checkbox:checked ~ .question_answer {
  max-height: 100vh;
  transition: max-height 1s ease-in-out 0.05s;
}

@media(max-width: 990px) {
  .questions {
    padding-right: 0;
    padding-left: 0;
  }

  .question_answer {
    padding-right: 75px;
  }
}

@media(max-width: 767px) {
  .questions {
    padding-right: 15px;
    padding-left: 15px;
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .questions_title {
    font-size: 26px;
    line-height: 110%;
    margin-bottom: 30px;
  }

  .questions_item {
    padding: 24px 0;
  }

  .questions_question {
    font-size: 20px;
    line-height: 120%;

    & svg {
      width: 28px;
      height: 28px;
    }
  }

  .question_answer {
    padding-right: 0;
  }

  .quesrions_checkbox:checked~.question_answer {
    padding-top: 24px;
  }
}