.header {
  background: url("./../img/background.jpg") center no-repeat;
}

.header_top {
  background: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;

  & .center {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-height: 80px;
  }

  &.scroll {
    background: black;
  }
}

.header_button {
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 15px;
  min-width: 200px;
  background: transparent;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;

  &:hover {
    cursor: pointer;
    background: #F03226;
    border-color: #F03226;
  }
}

.header_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: calc(80px + 60px);
  padding-bottom: 156px;
}

.header_img {
  margin-bottom: 60px;
}

.header_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 25px;
}

.header_subtitle {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 35px;
}

.header_content-button {
  background: #F03226;
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  padding: 18px 0;
  min-width: 240px;
  border: none;

  &:hover {
    background: white;
    color: #F03226;
    cursor: pointer;
  }
}

@media(max-width: 990px) {
  .header {
    background: url("./../img/background-tablet.jpg") center no-repeat;
  }

  .header_img {
    margin-bottom: 50px;
  }

  .header_content {
    padding-top: calc(80px + 50px);
    padding-bottom: 118px;
  }
}

@media(max-width: 767px) {
  .header_top .center {
    min-height: 60px;
  }

  .header_logo {
    width: 100px;
  }

  .header_button {
    padding: 12px;
    min-width: 130px;
    font-size: 14px;
    line-height: 16px;
  }

  .header_content {
    padding-top: calc(60px + 40px);
    padding-bottom: 60px;
  }

  .header_img {
    width: 152.5px;
    margin-bottom: 30px;
    height: 45px;
  }

  .header_title {
    font-size: 30px;
    line-height: 120%;
    margin-bottom: 15px;
  }

  .header_subtitle {
    font-size: 20px;
    line-height: 120%;
    margin-bottom: 25px;
  }
}

@media(max-width: 320px) {
  .header {
    background: url("./../img/background-mobile.jpg") center no-repeat;
  }
}